    onDayCellClickedForMultiDates(cell: IMyCalendarDay, isSecondCalendar?: boolean): void {
        if (cell.cmo === 1) {
            this.onPrevNavigateBtnClicked(true, isSecondCalendar);
        } else if (cell.cmo === 3) {
            this.onNextNavigateBtnClicked(true, isSecondCalendar);
        }
        const { dateFormat, monthLabels, maxSelected, closeSelectorOnDateSelect, showTime, returnFormat, selectedOrderBy } = this.opts;
        let selectedDates = [];
        const isSelected = this.selectedDates.find(n => this.utilService.isDateSame(n, cell.dateObj));
        if (!isSelected) {
            if (!maxSelected || (maxSelected && this.selectedDates.length < maxSelected)) {
                selectedDates = [...this.selectedDates, { ...cell.dateObj, value: this.utilService.formatDate(cell.dateObj, dateFormat, monthLabels) }];
            } else {
                selectedDates = [...this.selectedDates];
                if (this.showNotify) {
                    this.showNotify();
                }
            }
        } else {
            selectedDates = this.selectedDates.filter(n => n!== isSelected);
        }

        selectedDates = this.utilService.orderSelectedDates(selectedDates, selectedOrderBy);

        this.selectedDates = selectedDates;
        this.resetMonthYearSelect(isSecondCalendar);
    }

